








import {Component, Emit, Inject, Model, Prop, Provide, Vue, Watch} from 'vue-property-decorator'

@Component({
    components: {}
})
export default class BackButton extends Vue {

    @Prop({
        default : false
    })
    activeHandler ?: boolean;


    handleClick() {
        if (this.activeHandler) {
            this.$emit("click");
        } else {
            this.$router.back();
        }
    }


}
